import React from "react";
import FileSaver from "file-saver";
import { ColInfo, utils, write } from "xlsx";
import { RiFileExcel2Fill } from "react-icons/ri";

interface FileInterfaceProps {
  headers: string[];
  csvData: any[];
  fileName: string;
  wscols: ColInfo[] | undefined;
  headerFormat: any;
  buttonTitle: string;
  buttonColor: "blue" | "green";
}

const ExportCSV = (props: FileInterfaceProps) => {
  // ******** XLSX with object key as header *************
  // const fileType =
  //   "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8";
  // const fileExtension = ".xlsx";

  // const exportToCSV = (csvData, fileName) => {
  //   const ws = XLSX.utils.json_to_sheet(csvData);
  //   const wb = { Sheets: { data: ws }, SheetNames: ["data"] };
  //   const excelBuffer = XLSX.write(wb, { bookType: "xlsx", type: "array" });
  //   const data = new Blob([excelBuffer], { type: fileType });
  //   FileSaver.saveAs(data, fileName + fileExtension);
  // };

  // ******** XLSX with new header *************
  const fileType =
    "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8";
  const fileExtension = ".xlsx";

  const Heading = [props.headerFormat];

  const exportToCSV = (
    csvData: any[],
    fileName: string,
    wscols: ColInfo[] | undefined
  ) => {
    const ws = utils.json_to_sheet(Heading, {
      header: props.headers,
      skipHeader: true,
    });
    ws["!cols"] = wscols;
    utils.sheet_add_json(ws, csvData, {
      header: props.headers,
      skipHeader: true,
      origin: -1, //ok
    });
    const wb = { Sheets: { data: ws }, SheetNames: ["data"] };
    const excelBuffer = write(wb, { bookType: "xlsx", type: "array" });
    const data = new Blob([excelBuffer], { type: fileType });
    FileSaver.saveAs(data, fileName + fileExtension);
  };

  return (
    <div
      onClick={(e) => exportToCSV(props.csvData, props.fileName, props.wscols)}
      className={`flex flex-row items-center gap-2 w-max bg-${props.buttonColor}-600 hover:bg-${props.buttonColor}-800 text-white rounded cursor-pointer px-3 py-2 animate__animated animate__zoomIn`}
    >
      <div>
        <RiFileExcel2Fill className="text-2xl" />
      </div>
      <span>{props.buttonTitle}</span>
    </div>
  );
};

export default ExportCSV;

// This component is a presentational component which takes the data to download and file name as props. The exportToCSV method is invoked when the export button is clicked on line 20.
