import React, { Component } from "react";
import { MdAssignmentTurnedIn, MdInfoOutline } from "react-icons/md";
import { RiFileExcel2Fill } from "react-icons/ri";
import { DistrictLocationItem } from "../../../actions";
import ExcelExport from "../../../components/ExcelExport/ExcelExport";
import ExportCSV from "../../../components/ExcelExport/ExportCSV";
import Loading from "../../../components/Loading/Loading";
import { PositionAPI } from "../../../interfaces";
import { commaFy, DATE, search } from "../../../utils/functions";
import { PlacementReportInterface } from "../PlacementAlgorithm";

interface PlacementReportProps {
  selectedDistrict: DistrictLocationItem | null;
  selectedPosition: PositionAPI;
  chooseDistrict: () => void;
  placementReport: PlacementReportInterface[] | null;
  GetPlacementReport: (position_code: string, district_code: string) => void;
  GetDistrictName: (district_code: string) => string;
}
interface PlacementReportState {
  loading: boolean;
  searchValue: string;
}

interface ExportExcelReportProps {
  placementReport: PlacementReportInterface[];
  GetDistrictName: (district_code: string) => string;
  selectedDistrict: DistrictLocationItem;
  selectedPosition: PositionAPI;
  searchValue: string;
  editSearchValue: (value: string) => void;
}

const ExportExcelReport = (props: ExportExcelReportProps): JSX.Element => {
  const data = (
    search(
      props.placementReport,
      props.searchValue
    ) as PlacementReportInterface[]
  ).map((item, i) => ({
    id: (i + 1).toString(),
    names: `${item.fname} ${item.lname}`,
    phone_number: item.phone_number,
    email: item.email,
    position: `${item.position_name}(${item.position_code})`,
    marks: `${item.marks}`,
    gender: item.gender,
    disability: item.disability === true ? "Yes" : "No",
    in_education: item.in_education === true ? "Yes" : "No",
    requested_district: `${
      item.requested_district.values.length === 0
        ? "All districts"
        : item.requested_district.values.map(
            (val) =>
              ` ${props.GetDistrictName(val.toString())}${
                item.requested_district.values.length > 0 &&
                item.requested_district.values[
                  item.requested_district.values.length - 1
                ] === val
                  ? item.requested_district.strict === false
                    ? " and other districts"
                    : ""
                  : ""
              }`
          )
    }`,
    district: props.GetDistrictName(item.district_code.toString()),
    sector: item.sector,
    school_name: item.school_name,
  }));

  const wscols = [
    { wch: Math.max(...data.map((item) => item.id.length)) },
    { wch: Math.max(...data.map((item) => item.names.length)) },
    { wch: Math.max(...data.map((item) => item.phone_number.length)) },
    { wch: Math.max(...data.map((item) => item.email.length)) },
    { wch: Math.max(...data.map((item) => item.position.length)) },
    { wch: Math.max(...data.map((item) => item.marks.length)) },
    { wch: Math.max(...data.map((item) => item.gender.length)) },
    { wch: Math.max(...data.map((item) => item.disability.length)) },
    { wch: Math.max(...data.map((item) => item.in_education.length)) },
    { wch: Math.max(...data.map((item) => item.requested_district.length)) },
    { wch: Math.max(...data.map((item) => item.district.length)) },
    { wch: Math.max(...data.map((item) => item.sector.length)) },
    { wch: Math.max(...data.map((item) => item.school_name.length)) },
  ];

  const headers = [
    { label: "#", key: "id" },
    { label: "Names", key: "Names" },
    { label: "Phone number", key: "Phone_number" },
    { label: "Email", key: "Email" },
    { label: "Position", key: "Position" },
    { label: "Marks", key: "Marks" },
    { label: "Gender", key: "Gender" },
    { label: "Disability", key: "Disability" },
    { label: "In Education", key: "In_Education" },
    { label: "Preferences", key: "Preferences" },
    { label: "Placed District", key: "Placed_District" },
    { label: "Placed Sector", key: "Placed_Sector" },
    { label: "Placed School", key: "Placed_School" },
  ];

  return (
    <div>
      {/* Filter */}
      <div className="mt-2 flex flex-row items-center gap-2 w-full">
        <input
          placeholder="Search by teacher name or school name"
          type="text"
          value={props.searchValue}
          onChange={(e) => props.editSearchValue(e.target.value)}
          className="px-3 py-2 rounded border border-gray-400 w-full"
        />
        <div>
          <ExportCSV
            csvData={data}
            fileName={`${props.selectedDistrict.district_name}(${
              props.selectedDistrict.district_code
            }) - ${props.selectedPosition.position_title}(${
              props.selectedPosition.position_code
            }) TRS Placement Report on ${DATE(new Date().toString())}`}
            wscols={wscols}
            headers={[
              "id",
              "names",
              "phone_number",
              "email",
              "position",
              "marks",
              "gender",
              "disability",
              "in_education",
              "requested_district",
              "district",
              "sector",
              "school_name",
            ]}
            headerFormat={{
              id: "#",
              names: "Names",
              phone_number: "Phone number",
              email: "Email",
              position: "Position",
              marks: "Marks",
              gender: "Gender",
              disability: "Disability",
              in_education: "In Education",
              requested_district: "Preferences",
              district: "Placed District",
              sector: "Placed Sector",
              school_name: "Placed School",
            }}
            buttonTitle={"Export Excel"}
            buttonColor={"blue"}
          />
        </div>
      </div>
      <div className="w-full overflow-x-auto">
        {/* TABLE DATA */}
        <ExcelExport
          thead={
            <thead className="bg-blue-50 text-sm font-bold">
              <tr>
                <th className="px-2 py-2 border border-blue-300">#</th>
                <th className="px-2 py-2 border border-blue-300">Names</th>
                <th className="px-2 py-2 border border-blue-300">
                  Phone number
                </th>
                <th className="px-2 py-2 border border-blue-300">Email</th>
                <th className="px-2 py-2 border border-blue-300">Position</th>
                <th className="px-2 py-2 border border-blue-300">Marks</th>
                <th className="px-2 py-2 border border-blue-300">Gender</th>
                <th className="px-2 py-2 border border-blue-300">Disability</th>
                <th className="px-2 py-2 border border-blue-300">
                  In Education
                </th>
                <th className="px-2 py-2 border border-blue-300">
                  Preferences
                </th>
                <th className="px-2 py-2 border border-blue-300">
                  Placed District
                </th>
                <th className="px-2 py-2 border border-blue-300">
                  Placed Sector
                </th>
                <th className="px-2 py-2 border border-blue-300">
                  Placed School
                </th>
              </tr>
            </thead>
          }
          rowFormat={(item: {
            id: string;
            names: string;
            phone_number: string;
            email: string;
            position: string;
            marks: string;
            gender: string;
            disability: string;
            in_education: string;
            requested_district: string;
            district: string;
            sector: string;
            school_name: string;
          }): JSX.Element => {
            return (
              <tr
                key={item.id}
                className={`${
                  parseInt(item.id) % 2 === 0 ? "bg-gray-100" : ""
                } hover:bg-blue-100 hover:text-blue-700 group`}
              >
                <td className="px-2 py-2 border border-gray-300 group-hover:border-blue-400">
                  {item.id}
                </td>
                <td className="px-2 py-2 border border-gray-300 group-hover:border-blue-400">
                  <div className="w-48">{item.names}</div>
                </td>
                <td className="px-2 py-2 border border-gray-300 group-hover:border-blue-400">
                  <div className="w-24">{item.phone_number}</div>
                </td>
                <td className="px-2 py-2 border border-gray-300 group-hover:border-blue-400">
                  {item.email}
                </td>
                <td className="px-2 py-2 border border-gray-300 group-hover:border-blue-400">
                  {item.position})
                </td>
                <td className="px-2 py-2 border border-gray-300 group-hover:border-blue-400">
                  {item.marks}
                </td>
                <td className="px-2 py-2 border border-gray-300 group-hover:border-blue-400">
                  {item.gender}
                </td>
                <td className="px-2 py-2 border border-gray-300 group-hover:border-blue-400">
                  {item.disability}
                </td>
                <td className="px-2 py-2 border border-gray-300 group-hover:border-blue-400">
                  {item.in_education}
                </td>
                <td className="px-2 py-2 border border-gray-300 group-hover:border-blue-400">
                  <div className="w-48">{item.requested_district}</div>
                </td>
                <td className="px-2 py-2 border border-gray-300 group-hover:border-blue-400">
                  {item.district}
                </td>
                <td className="px-2 py-2 border border-gray-300 group-hover:border-blue-400">
                  {item.sector}
                </td>
                <td className="px-2 py-2 border border-gray-300 group-hover:border-blue-400">
                  <div className="truncate">{item.school_name}</div>
                </td>
              </tr>
            );
          }}
          data={data}
        />
      </div>
    </div>
  );
};

export class PlacementReport extends Component<
  PlacementReportProps,
  PlacementReportState
> {
  constructor(props: PlacementReportProps) {
    super(props);

    this.state = {
      loading: false,
      searchValue: "",
    };
  }
  render() {
    if (this.props.selectedDistrict === null) {
      return (
        <div className="bg-white rounded p-4 flex flex-col items-center justify-center gap-3">
          <div>
            <MdInfoOutline className="text-7xl" />
          </div>

          <div className="text-center text-xl font-light">
            No district select, please you are required to choose district
          </div>
          <div className="mt-4 flex flex-row items-center justify-center gap-3 w-full">
            <div
              onClick={() => this.props.chooseDistrict()}
              className="btn bg-blue-500 text-white cursor-pointer hover:bg-blue-700"
            >
              Choose a district
            </div>
          </div>
        </div>
      );
    }
    if (this.props.placementReport === null) {
      return (
        <div className="bg-white rounded p-4 pt-10 mt-5">
          <Loading />
          <div className="ml-4 -mt-5 text-xl font-light">
            Loading, please wait...
          </div>
        </div>
      );
    }
    return (
      <div className="bg-white p-3">
        <div className="flex flex-row items-center gap-4 justify-between">
          <div className="">
            <div className="flex flex-row items-center gap-2 text-black">
              <div>
                <MdAssignmentTurnedIn className="text-5xl animate__animated animate__zoomIn" />
              </div>
              <div>
                <div className="text-2xl font-bold flex flex-row items-center gap-3">
                  <span>Placement report</span>
                  <div className="rounded-full bg-blue-600 text-white font-extrabold text-xl px-2">
                    {commaFy(this.props.placementReport.length)} teachers
                  </div>
                </div>
                <div className="text-sm font-light text-black">
                  This the placement report, it is generated on the district
                  level. choose a district where you want the report
                </div>
                <div className="flex flex-row items-center gap-2 mt-1">
                  <div className="bg-blue-100 rounded-full px-2 text-sm text-blue-800">
                    Position: {this.props.selectedPosition.position_title}
                  </div>
                  <div className="bg-blue-100 rounded-full px-2 text-sm text-blue-800">
                    District: {this.props.selectedDistrict.district_name}
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div>
            <div className="font-normal text-sm">
              Position: {this.props.selectedPosition.position_title}
            </div>
            <div className="font-normal text-sm">
              District: {this.props.selectedDistrict?.district_name}
            </div>
            <div
              className="px-2 py-1 text-blue-700 border-2 border-blue-600 rounded bg-blue-100 hover:bg-blue-600 hover:text-white w-max cursor-pointer"
              onClick={() => this.props.chooseDistrict()}
            >
              {this.props.selectedDistrict === null
                ? "Choose"
                : `${this.props.selectedDistrict.district_name} - Change`}{" "}
              district
            </div>
          </div>
        </div>
        {/* table data */}
        <ExportExcelReport
          placementReport={this.props.placementReport}
          GetDistrictName={this.props.GetDistrictName}
          selectedDistrict={this.props.selectedDistrict}
          selectedPosition={this.props.selectedPosition}
          searchValue={this.state.searchValue}
          editSearchValue={(value: string) => {
            this.setState({ searchValue: value });
          }}
        />
      </div>
    );
  }
}

export default PlacementReport;
