import axios from "axios";
import React, { Component, Fragment } from "react";
import { BiErrorAlt } from "react-icons/bi";
import {
  MdChangeCircle,
  MdCheckCircle,
  MdInfoOutline,
  MdWarning,
} from "react-icons/md";
import { connect } from "react-redux";
import {
  applicationPeriodsStoreInterface,
  Auth,
  positionsStoreInterface,
  FC_getLocations,
  LocationAPI,
} from "../../actions";
import { FC_GetPositionsByInstitution } from "../../actions/settings";
import Loading from "../../components/Loading/Loading";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import { PositionAPI } from "../../interfaces";
import { StoreState } from "../../reducers";
import { API } from "../../utils/api";
import { errorToText } from "../../utils/functions";
import Districts from "./Districts/Districts";
import PlacementReport from "./PlacementReport/PlacementReport";
import { Positions } from "./Positions/Positions";
import { PostsRequests } from "./PostsRequests/PostsRequests";

export interface PlacementReportInterface {
  _id: string;
  requested_district: {
    values: number[];
    strict: boolean;
  };
  status: number;
  request_id: string;
  district_code: number;
  district: string;
  sector_code: string;
  sector: string;
  school_code: string;
  school_name: string;
  position_code: string;
  position_name: string;
  user_id: string;
  email: string;
  phone_number: string;
  fname: string;
  lname: string;
  gender: string;
  session_id: string;
  marks: number;
  in_education: boolean;
  disability: boolean;
  createdAt: string;
  updatedAt: string;
}

export interface SchoolPostsRequests {
  _id: string;
  district_code: string;
  district: string;
  sector_code: string;
  sector: string;
  school_code: string;
  school_name: string;
  position_code: string;
  position_name: string;
  requested_teacher: number;
  placed_teachers: number;
  academic_year_id: string;
}

export interface DistrictPostsRequests {
  _id: string;
  totalRequestedTeacher: number;
  totalPlacedTeachers: number;
}

interface PlacementAlgorithmProps {
  auth: Auth;
  periods: applicationPeriodsStoreInterface;
  FC_GetPositionsByInstitution: (
    institution_id: string,
    callback: (status: boolean, msg: string, data?: PositionAPI[]) => void
  ) => void;
}
interface PlacementAlgorithmState {
  loading: boolean;
  documentType: "PostRequests" | "PlacementReport" | "GeneralReport";
  posts_requests: DistrictPostsRequests[] | null;
  error: string;
  success: string;
  selectedPosition: PositionAPI | null;
  choosePosition: boolean;
  placementReport: PlacementReportInterface[] | null;
  districts: LocationAPI | null;
  selected_district_code: string;
  select_district_modal: boolean;
  positions: PositionAPI[] | null;
}

class _PlacementAlgorithm extends Component<
  PlacementAlgorithmProps,
  PlacementAlgorithmState
> {
  constructor(props: PlacementAlgorithmProps) {
    super(props);

    this.state = {
      loading: false,
      documentType: "PlacementReport",
      posts_requests: null,
      error: "",
      success: "",
      selectedPosition: null,
      choosePosition: false,
      placementReport: null,
      districts: null,
      selected_district_code: "",
      select_district_modal: false,
      positions: null,
    };
  }

  GetDistrictName = (district_code: string): string => {
    let district = this.state.districts?.districts.find(
      (itm) => itm.district_code === district_code
    );
    if (district === undefined) {
      return "";
    } else {
      return district.district_name;
    }
  };

  GetDistrictsPostsRequests = async (position_code: string) => {
    this.setState({ loading: true });
    try {
      const res = await axios.get<DistrictPostsRequests[]>(
        `${API.PLACEMENT}/schoolrequest/district/position/summary/${position_code}`
      );
      if (res) {
        console.log("Res: ", res.data);
        this.setState({
          loading: false,
          posts_requests: res.data,
          error: "",
          success: "",
        });
      }
    } catch (error: any) {
      this.setState({ error: errorToText(error), loading: false });
      console.log("Error: ", { ...error });
    }
  };

  GetDistrictPlacementReport = async (
    position_code: string,
    district_code: string
  ) => {
    this.setState({ loading: true });
    try {
      const res = await axios.get<PlacementReportInterface[]>(
        `${API.PLACEMENT}/placedcandidate/district/position/${district_code}/${position_code}`
      );
      if (res) {
        console.log("Res: ", res.data);
        this.setState({
          loading: false,
          placementReport: res.data,
          error: "",
          success: "",
        });
      }
    } catch (error: any) {
      this.setState({ error: errorToText(error), loading: false });
      console.log("Error: ", { ...error });
    }
  };

  componentDidMount = () => {
    // Load districts
    if (this.state.districts === null) {
      this.setState({ loading: true });
      FC_getLocations(
        (error: boolean, locations: LocationAPI | null, msg: string) => {
          if (locations !== null) {
            this.setState({
              districts: locations,
              loading: false,
              error: "",
            });
          } else {
            if (error === false) {
              this.setState({ error: msg, loading: false, districts: null });
            }
          }
        }
      );
    }
    // Load positions
    if (
      this.state.positions === null &&
      this.props.auth.education_institution_id !== null
    ) {
      this.setState({ loading: true });
      this.props.FC_GetPositionsByInstitution(
        this.props.auth.education_institution_id,
        (status: boolean, msg: string, data?: PositionAPI[]) => {
          this.setState({ loading: status });
          if (status === false && data !== undefined) {
            this.setState({ positions: data, loading: false });
          }
          if (status === false && msg !== "") {
            this.setState({ error: msg, loading: false });
          }
        }
      );
    }
  };

  render() {
    if (this.state.loading === true) {
      return (
        <div className="bg-white rounded p-4 pt-10 mt-5">
          <Loading />
          <div className="ml-4 -mt-5 text-xl font-light">
            Loading, please wait...
          </div>
        </div>
      );
    }
    return (
      <Fragment>
        {this.state.selectedPosition === null ? (
          <div className="bg-white rounded p-4 mt-5 flex flex-col items-center justify-center gap-3">
            <div>
              <MdInfoOutline className="text-7xl" />
            </div>

            <div className="text-center text-xl font-light">
              No position select, please you are required to choose position
            </div>
            <div className="mt-4 flex flex-row items-center justify-center gap-3 w-full">
              <div
                onClick={() => this.setState({ choosePosition: true })}
                className="btn bg-blue-500 text-white cursor-pointer hover:bg-blue-700"
              >
                Choose a position
              </div>
            </div>
          </div>
        ) : (
          <div className="pr-2 -ml-2 lg:ml-0">
            <div className="flex flex-row items-baseline justify-between w-full mt-3">
              <div className="flex flex-col">
                <div className="font-extrabold text-3xl">
                  Candidate Placements
                </div>
                <div className="text-base flex flex-row items-center bg-white mt-3 rounded-t-md overflow-hidden">
                  <div
                    onClick={() => {
                      if (this.state.documentType !== "PostRequests") {
                        if (
                          this.state.posts_requests === null &&
                          this.state.selectedPosition !== null
                        ) {
                          // Request
                          this.GetDistrictsPostsRequests(
                            this.state.selectedPosition.position_code!
                          );
                        }
                        this.setState({ documentType: "PostRequests" });
                      }
                    }}
                    className={`border-b-4 ${
                      this.state.documentType === "PostRequests"
                        ? "border-blue-600 font-bold text-blue-600 cursor-not-allowed"
                        : "border-white cursor-pointer hover:border-blue-500 hover:text-blue-600"
                    } text-center w-max px-4 py-2 pt-3`}
                  >
                    Posts Requests
                  </div>
                  <div
                    onClick={() => {
                      if (this.state.documentType !== "PlacementReport") {
                        this.setState({ documentType: "PlacementReport" });
                        if (
                          this.state.placementReport === null &&
                          this.state.selectedPosition !== null &&
                          this.state.selectedPosition.position_code !== null
                        ) {
                          this.GetDistrictPlacementReport(
                            this.state.selectedPosition.position_code!,
                            this.state.selected_district_code
                          );
                        }
                      }
                    }}
                    className={`border-b-4 ${
                      this.state.documentType === "PlacementReport"
                        ? "border-blue-600 font-bold text-blue-600 cursor-not-allowed"
                        : "border-white cursor-pointer hover:border-blue-500 hover:text-blue-600"
                    } text-center w-max px-4 py-2 pt-3`}
                  >
                    Placement Report
                  </div>
                  <div
                    onClick={() => {
                      if (this.state.documentType !== "GeneralReport") {
                        this.setState({ documentType: "GeneralReport" });
                      }
                    }}
                    className={`border-b-4 ${
                      this.state.documentType === "GeneralReport"
                        ? "border-blue-600 font-bold text-blue-600 cursor-not-allowed"
                        : "border-white cursor-pointer hover:border-blue-500 hover:text-blue-600"
                    } text-center w-max px-4 py-2 pt-3`}
                  >
                    General Report
                  </div>
                </div>
              </div>
              <div>
                <div
                  onClick={() => this.setState({ choosePosition: true })}
                  className="bg-blue-100 text-blue-800 px-3 pr-2 py-1 rounded border-2 border-white hover:border-blue-600 flex flex-row items-center gap-3 cursor-pointer group"
                >
                  <div className="flex flex-col">
                    <div className="font-light text-lg">Selected position</div>
                    <div className="text-light font-extrabold -mt-1">
                      {this.state.selectedPosition.position_title}
                    </div>
                  </div>
                  <div>
                    <MdChangeCircle className={`text-4xl`} />
                  </div>
                </div>
              </div>
            </div>
            {this.state.error !== "" && (
              <div className="flex flex-row items-center gap-3 rounded px-3 py-2 w-full cursor-pointer my-2 bg-white border border-red-600">
                <div>
                  <BiErrorAlt className="text-3xl text-red-600" />
                </div>
                <div className="flex flex-col">
                  <span className="font-bold text-lg text-red-600">
                    {this.state.error}
                  </span>
                </div>
              </div>
            )}
            {this.state.success !== "" && (
              <div className="flex flex-row items-center gap-3 rounded-lg px-3 py-2 w-full cursor-pointer my-2 bg-white border border-green-600">
                <div>
                  <MdCheckCircle className="text-3xl text-green-600" />
                </div>
                <div className="flex flex-col">
                  <span className="font-bold text-lg text-green-600">
                    {this.state.success}
                  </span>
                </div>
              </div>
            )}
            {this.state.documentType === "PostRequests" &&
              this.state.posts_requests !== null && (
                <PostsRequests
                  postRequests={this.state.posts_requests}
                  GetDistrictName={this.GetDistrictName}
                />
              )}
            {this.state.documentType === "PlacementReport" && (
              <PlacementReport
                selectedDistrict={
                  this.state.districts === null
                    ? null
                    : this.state.districts.districts.find(
                        (itm) =>
                          itm.district_code ===
                          this.state.selected_district_code
                      ) === undefined
                    ? null
                    : this.state.districts.districts.find(
                        (itm) =>
                          itm.district_code ===
                          this.state.selected_district_code
                      )!
                }
                placementReport={this.state.placementReport}
                selectedPosition={this.state.selectedPosition}
                chooseDistrict={() =>
                  this.setState({ select_district_modal: true })
                }
                GetPlacementReport={this.GetDistrictPlacementReport}
                GetDistrictName={this.GetDistrictName}
              />
            )}
          </div>
        )}
        {this.state.choosePosition === true && this.state.positions !== null && (
          <Modal
            backDrop={true}
            theme={Themes.default}
            close={() => {
              this.setState({ choosePosition: false });
            }}
            backDropClose={true}
            widthSizeClass={ModalSize.large}
            displayClose={true}
            padding={{
              title: true,
              body: true,
            }}
            title={<div className="text-2xl font-bold">Choose position</div>}
          >
            <Positions
              selectPosition={(position: PositionAPI) => {
                if (position.position_code !== null) {
                  this.state.documentType === "PostRequests" &&
                    this.GetDistrictsPostsRequests(position.position_code);
                  this.state.documentType === "PlacementReport" &&
                    this.state.selected_district_code !== "" &&
                    this.GetDistrictPlacementReport(
                      position.position_code,
                      this.state.selected_district_code
                    );
                }
                position.position_code !== null &&
                  this.setState({
                    selectedPosition: position,
                    choosePosition: false,
                  });
              }}
              positions={this.state.positions}
              selected_position_code={
                this.state.selectedPosition === null
                  ? ""
                  : this.state.selectedPosition.position_code!
              }
            />
          </Modal>
        )}
        {this.state.select_district_modal === true && (
          <Modal
            backDrop={true}
            theme={Themes.default}
            close={() => this.setState({ select_district_modal: false })}
            backDropClose={true}
            widthSizeClass={ModalSize.medium}
            displayClose={true}
            padding={{
              title: true,
              body: true,
            }}
            title={<div className="text-2xl">Filter by district</div>}
          >
            {this.state.districts === null ? (
              <div>Loading...</div>
            ) : (
              this.state.selectedPosition !== null &&
              this.state.selectedPosition.position_code !== null && (
                <div className="-mt-5">
                  <Districts
                    districts={this.state.districts}
                    selected_district_code={this.state.selected_district_code}
                    selectDistrict={(district_code: string) => {
                      this.setState({
                        selected_district_code: district_code,
                        select_district_modal: false,
                      });
                      this.GetDistrictPlacementReport(
                        this.state.selectedPosition!.position_code!,
                        district_code
                      );
                    }}
                  />
                </div>
              )
            )}
          </Modal>
        )}
      </Fragment>
    );
  }
}
const mapStateToProps = ({
  auth,
  periods,
}: StoreState): {
  auth: Auth;
  periods: applicationPeriodsStoreInterface;
} => {
  return {
    auth: auth,
    periods: periods,
  };
};

export const PlacementAlgorithm = connect(mapStateToProps, {
  FC_GetPositionsByInstitution,
})(_PlacementAlgorithm);
