import React, { Component } from "react";
import { commaFy } from "../../../utils/functions";
import { DistrictPostsRequests } from "../PlacementAlgorithm";

interface PostsRequestsProps {
  postRequests: DistrictPostsRequests[];
  GetDistrictName: (district_code: string) => string;
}
interface PostsRequestsState {
  searchValue: string;
  loading: boolean;
}
export class PostsRequests extends Component<
  PostsRequestsProps,
  PostsRequestsState
> {
  constructor(props: PostsRequestsProps) {
    super(props);

    this.state = {
      loading: false,
      searchValue: "",
    };
  }
  render() {
    return (
      <div className="p-0 border border-white text-left bg-white">
        <div className="my-3 mx-3">
          <input
            type="text"
            placeholder="Search by district name"
            className="px-3 py-2 rounded border border-gray-400 w-full"
          />
        </div>
        <table className="w-full">
          <thead>
            <tr className="bg-blue-50">
              <th className="px-3 py-2">#</th>
              <th className="px-3 py-2">District code</th>
              <th className="px-3 py-2">District name</th>
              <th className="px-3 py-2">Total requests</th>
              <th className="px-3 py-2">Total placed</th>
              <th className="px-3 py-2"></th>
            </tr>
          </thead>
          <tbody>
            {this.props.postRequests.length === 0 ? (
              <tr>
                <td
                  colSpan={6}
                  className="py-6 text-center text-2xl font-extralight"
                >
                  No result found!
                </td>
              </tr>
            ) : (
              this.props.postRequests.map((item, i) => (
                <tr className={`${i % 2 !== 0 ? "bg-gray-100" : "bg-white"}`}>
                  <td className="px-3 py-1">{i + 1}</td>
                  <td className="px-3 py-1">{item._id}</td>
                  <td className="px-3 py-1">
                    {this.props.GetDistrictName(item._id)}
                  </td>
                  <td className="px-3 py-1">
                    {commaFy(item.totalRequestedTeacher)}
                  </td>
                  <td className="px-3 py-1">
                    {commaFy(item.totalPlacedTeachers)}
                  </td>
                  <td className="px-1 py-1 flex flex-row items-center justify-end">
                    <div className="bg-blue-100 px-2 py-1 text-center w-max">
                      Action
                    </div>
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>
    );
  }
}
