import React, { Component } from "react";
import { PositionAPI } from "../../../interfaces";
import { search } from "../../../utils/functions";

interface PositionsProps {
  positions: PositionAPI[];
  selectPosition: (position: PositionAPI) => void;
  selected_position_code: string;
}
interface PositionsState {
  loading: boolean;
  searchValue: string;
}

export class Positions extends Component<PositionsProps, PositionsState> {
  constructor(props: PositionsProps) {
    super(props);

    this.state = {
      loading: false,
      searchValue: "",
    };
  }
  render() {
    return (
      <div className="-mt-5">
        <div className="flex flex-col">
          <span>Search by position name</span>
          <input
            type="text"
            value={this.state.searchValue}
            onChange={(e) => {
              this.setState({ searchValue: e.target.value });
            }}
            className="px-3 py-2 rounded border-2 border-blue-600 w-full"
          />
        </div>
        <div>
          <table className="w-full text-left mt-2">
            <thead className="bg-blue-50">
              <tr>
                <th className="px-3 py-2">#</th>
                <th className="px-3 py-2">Code</th>
                <th className="px-3 py-2">Name</th>
                <th className="px-3 py-2"></th>
              </tr>
            </thead>
            <tbody>
              {search(this.props.positions, this.state.searchValue).length ===
              0 ? (
                <tr>
                  <td
                    colSpan={4}
                    className="text-xl font-light text-center py-5"
                  >
                    No result found!
                  </td>
                </tr>
              ) : (
                (
                  search(
                    this.props.positions,
                    this.state.searchValue
                  ) as PositionAPI[]
                ).map((item, i) => (
                  <tr
                    key={i + 1}
                    onClick={() => this.props.selectPosition(item)}
                    className={`${
                      this.props.selected_position_code === item.position_code
                        ? "bg-blue-200 font-bold text-blue-700"
                        : i % 2 !== 0
                        ? "bg-gray-100"
                        : "bg-white"
                    } group hover:text-blue-800 hover:bg-blue-100 cursor-pointer`}
                  >
                    <td className="px-3 py-2">{i + 1}</td>
                    <td className="px-3 py-2">{item.position_code}</td>
                    <td className="px-3 py-2">{item.position_title}</td>
                    <td className="flex flex-row items-center justify-end py-2 px-1">
                      {this.props.selected_position_code !==
                        item.position_code && (
                        <div className="rounded px-2 py-1 text-base font-light bg-blue-100 text-black group-hover:bg-blue-600 group-hover:text-white cursor-pointer">
                          Choose
                        </div>
                      )}
                    </td>
                  </tr>
                ))
              )}
              <tr></tr>
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}

export default Positions;
