import React, { Component } from "react";
import { DistrictLocationItem, LocationAPI } from "../../../actions";
import { search } from "../../../utils/functions";

interface DistrictsProps {
  districts: LocationAPI;
  selected_district_code: string;
  selectDistrict: (district_code: string) => void;
}
interface DistrictsState {
  loading: boolean;
  searchValue: string;
}

export class Districts extends Component<DistrictsProps, DistrictsState> {
  constructor(props: DistrictsProps) {
    super(props);

    this.state = {
      loading: false,
      searchValue: "",
    };
  }
  render() {
    return (
      <div className="bg-white">
        <div className="my-3">
          <input
            placeholder="Search by district name"
            type="text"
            value={this.state.searchValue}
            onChange={(e) => this.setState({ searchValue: e.target.value })}
            className="px-3 py-2 rounded border border-gray-400 w-full"
          />
        </div>
        <div>
          <table className="w-full text-left text-sm">
            <thead>
              <tr className="bg-blue-600 text-white">
                <th className="px-3 py-2">#</th>
                <th className="px-3 py-2">Code</th>
                <th className="px-3 py-2">District name</th>
                <th className="px-3 py-2"></th>
              </tr>
            </thead>
            <tbody>
              {search(this.props.districts.districts, this.state.searchValue)
                .length === 0 ? (
                <tr>
                  <td
                    colSpan={4}
                    className="py-6 text-center font-extralight text-2xl"
                  >
                    No result found!
                  </td>
                </tr>
              ) : (
                (
                  search(
                    this.props.districts.districts,
                    this.state.searchValue
                  ) as DistrictLocationItem[]
                ).map((item, i) => (
                  <tr
                    onClick={() =>
                      this.props.selectDistrict(item.district_code)
                    }
                    className={`group ${
                      item.district_code === this.props.selected_district_code
                        ? "bg-blue-100 font-bold text-blue-600"
                        : i % 2 !== 0
                        ? "bg-gray-100"
                        : "bg-white"
                    } hover:bg-blue-100 hover:text-blue-600 cursor-pointer`}
                  >
                    <td className="px-3 py-2">{i + 1}</td>
                    <td className="px-3 py-2">{item.district_code}</td>
                    <td className="px-3 py-2">{item.district_name}</td>
                    <td className="p-1 flex flex-row items-center justify-end">
                      {item.district_code !==
                        this.props.selected_district_code && (
                        <div className="cursor-pointer rounded bg-blue-100 group-hover:bg-blue-600 group-hover:text-white px-2 py-1">
                          Choose
                        </div>
                      )}
                    </td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}

export default Districts;
